<template>
	<view>
		<!-- 订单列表 -->
		<!-- <mescroll-uni :ref="'mescrollRef'+index" @init="mescrollInit" height="100%" top="60" :down="downOption" @down="downCallback" :up="upOption" @up="upCallback" @emptyclick="emptyClick"> -->
			<view class="wait-list">
				<view class="info" >
					<view class="parm" @tap="goDetail(item)">
						<view class="parm-txt title">{{item.type}}/{{item.duration}}</view>
						<view class="parm-txt">{{item.origin}}</view>
						<template v-if="item.Distance">
							<view class="parm-txt">距离：{{item.Distance}}公里</view>
						</template>
						
						<view class="parm-txt"><span>{{item.label}}</span></view>
						<view class="parm-txt">上门时间：{{item.door_time}}</view>
						<template v-if="item.reason">
							<view  class="parm-txt">取消原因：{{item.reason}}</view>
						</template>
						
					</view>
					<view class="parm">
						<view class="price">{{item.budget}}元</view>
						<!-- <view class="reason">取消原因{{item.reason}}</view> -->
					</view>
				</view>
				<view class="btn-group">
					<template v-if="btn[0] != ' '">
						<view class="btn" @tap="deleteOrder(index)">{{btn[0]}}</view>
					</template>
					<template v-if="btn[1] != ' '">
						<template v-if="btn[1] == '联系客服'">
							<view  class="btn active" @click="show = true">联系客服</view>
							<u-mask :show="show" @click="show = false">
									<view class="warp">
										<view class="rect" @tap.stop>
											<button type="default" open-type="contact" class="btn active">{{btn[1]}}</button>
											<view  class="btn active" @tap="go">拨打电话</view>
										</view>
									</view>
								</u-mask>
						</template>
						<template v-else>
							<view class="btn active" @tap="openpage(item)">{{btn[1]}}</view>
						</template>
					</template>
				</view>
			</view>
		<!-- </mescroll-uni> -->
			<!-- 拨打客服电话或者小程序客户 -->
			<!-- <u-select v-model="showcot" :list="list" @confirm="confirm"></u-select> -->
	</view>	
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollMoreItemMixin from "@/components/mescroll-uni/mixins/mescroll-more-item.js";
	
	export default {
		mixins: [MescrollMixin,MescrollMoreItemMixin], 
		props:{
			item: Object,
			index: Number,
			btn:Array,
			keys:Number,
			color:String,
			isclick:Boolean
		},
		data() {
			return {
				show: false,
				list:[
					{
						value: '1',
						label: '聊天客服'
					},
					{
						value: '2',
						label: '客服电话'
					}
				],
				index1:0,
				itemData:[],
				cancel:'background:#cccccc',
				upOption:{
					use:false
				},
				downOption:{
					auto:false,
				},
			}
		},
		onReady() {
			console.log("要传递的值");
			console.log(this.item);
			console.log(this.key);
			this.itemData.push(this.item);
		},
		methods:{
			//客服会话功能
			// confirm(e) {
			// 	console.log(e);
			// }
			// chosekefu(){
			// 	uni.showModal({
			// 	    title: '提示',
			// 		cancelText:"微信客服",
			// 		confirmText:"拨打热线",
			// 	    content: '选择沟通方式',
			// 	    success: function (res) {
			// 	        if (res.confirm) {
			// 	            console.log('用户点击确定');
			// 				this.iskehu = 0
			// 	        } else if (res.cancel) {
			// 	            console.log('用户点击取消');
			// 				this.iskehu = 1
			// 	        }
			// 	    }
			// 	});
			// }
			//跳转到详情页
			goDetail: function(item) {
				let detail = {
					Distance: item.Distance,
					budget: item.budget,
					code: item.code,
					content: item.content,
					delete: item.delete,
					destination: item.destination,
					door_time: item.door_time,
					duration: item.duration,
					label: item.label,
					longitude: item.longitude,
					name: item.name,
					origin: item.origin,
					send: item.send,
					state: item.state,
					tel: item.tel,
					type: item.type,
					uid: item.uid
				};
				if(this.isclick){
					uni.navigateTo({
						url: '../../pages/order-content/order-content?detailDate=' + encodeURIComponent(JSON.stringify(detail))
					});
				}
				// uni.navigateTo({
				// 	url: '../list2detail-detail/list2detail-detail?detailDate=' + encodeURIComponent(JSON.stringify(detail))
				// });
			},
			openpage(item){
				let server = {
					Distance: item.Distance,
					budget: item.budget,
					code: item.code,
					content: item.content,
					delete: item.delete,
					destination: item.destination,
					door_time: item.door_time,
					duration: item.duration,
					label: item.label,
					longitude: item.longitude,
					name: item.name,
					origin: item.origin,
					send: item.send,
					state: item.state,
					tel: item.tel,
					type: item.type,
					uid: item.uid
				};
					if(this.btn[1] =="立即上门"){
						uni.navigateTo({
							url: '../../pages/order-content/order-content?detailDate=' + encodeURIComponent(JSON.stringify(server))
						})
					}else{
						this.go()
					}
			},
			//删除订单
			deleteOrder(index){
				this.$emit("deleteOrder",index)
			},
			//拨打客服电话
			go(){
			 	uni.makePhoneCall({
			 	
			 	// 手机号
			    phoneNumber: '400-0015-021', 
			
				// 成功回调
				success: (res) => {
					console.log('调用成功!')	
				},
			
				// 失败回调
				fail: (res) => {
					console.log('调用失败!')
				}
				
			  });
			}
		}
	}
	
</script>

<style scoped>
	/* 订单列表 */
	.wait-list{
		
		/* border-bottom: 1upx solid #c5c4d5; */
		padding-top: 50upx;
	}
	.wait-list .btn-group{
		display: flex;
		justify-content: end;
		padding: 15upx;
		justify-content: flex-end;
		border-bottom: 1upx solid #c5c4d5;
	}
	.btn-group .active{
		background-color: #00AAEB;
		color: #FFFFFF !important;
		border: none !important;
	}
	.info{
		padding-left: 25upx;
		padding-right: 25upx;
		
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.parm{
		line-height: 28upx;
	}
	view{
		line-height: 2.15;
	}
	.parm .title{
		font-size: 32upx;
		font-weight: bold;
	}
	.parm .price{
		text-align: center;
		color:#FA5741 ;
	}
	.parm .pic image{
		width: 168upx;
		height: 168upx;
		border-radius: 100%;
	}
	.btn-group .btn {
		font-size: 28upx;
		border-radius: 28upx;
		border: 1upx solid #CCCCCC;
		color:#CCCCCC ;
		padding-left: 20upx;
		padding-right: 20upx;
		line-height: 1.5;
		margin-left: 48upx;
		border-bottom: 1upx solid #c5c4d5 !important;
	}
	button{
		-webkit-appearance: none;
	}
	button::after{
		border: none;
	}
</style>